/*------------------------------------*\
    #Carousel
\*------------------------------------*/
.carousel-dots {
    .owl-dots {

        &.disabled {
            display: flex;
        }

        display        : flex;
        justify-content: center;
        margin-top     : 40px;
        @media #{$minLarge} {
            margin-top: 133px;
            justify-content: flex-start;
            padding-left: 120px;
        }
        @media #{$noteBook} {
            margin-top: 100px;
        }
        .owl-dot {
            width           : 16px;
            height          : 16px;
            background-color: #E9E9E9;
            border-radius   : 50%;
            margin-right    : 10px;

            &.active {
                background-color: var(--global--color-secondary);
            }
            &:last-child{
                margin-right: 0;
            }

            &:hover,
            &:focus {
                outline: none;
            }
        }
    }

}


.carousel-navs {
    .owl-nav {

        @media #{$maxTablet} {
            display: none;
        }
        &.disabled {
            display: flex;
        }

        [class*="owl-"] {
            position : absolute;
            top      : 50%;
            transform: translateY(-50%);
            outline  : none;
            width: 45px;
            height: 25px;

            span {
                display: none;
            }
        }

        button {

            &.owl-prev {
                left     : 0px;
                transform: rotate(-180deg) translateY(50%);
                background: url("../images/icons/right-arrow.png") no-repeat center;
            }
    
            &.owl-next {
                right: 0px;
                background: url("../images/icons/right-arrow.png") no-repeat center;
            }
        }
    }
}

/* Custom, iPhone Retina */
@media only screen and (min-width: 320px) and (max-width: 767px) {
    .carousel-dots .owl-controls {
        margin-top: 20px;
    }
}