.about {
    padding-top:70px;
    .heading {
        margin-bottom: 40px;
    }

    .about-action {
        display: flex;

        @media #{$maxSmall} {
            flex-direction: column;
            align-items   : center;
        }

        .btn {
            margin-right: 30px;

            &:last-child {
                margin-right: 0;
            }

            @media #{$maxSmall} {
                margin-right : 0;
                margin-bottom: 25px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }

    img {
        @media #{$maxTablet} {
            display: block;
            margin : 0 auto;
        }
    }
    &.about-2 {
        position: relative;
        padding-top: 0;
        padding-bottom: 0;
        @media #{$maxSmall} {
            padding-top: 50px;
        }
        .about-img {
            position: absolute;
            top: -50px;
            width: 60%;
            right: 0;
            @media screen and (max-width: 1400px) {
                top: 0;
            }
            @media #{$maxTablet} {
                top: 25px;
            }
            @media #{$maxSmall} {
                display: none
            }
            img {
                object-fit:contain;
                width: 100%;
                height: 100%;
            }
        }
        .about-img-plcaeholder {
            min-height: 500px;
            @media #{$maxTablet} {
                min-height: 350px;
            }
            @media #{$maxSmall} {
                display: none
            }
        }
    }
}