/*------------------------------------*\
    #Testimonial
\*------------------------------------*/
.testimonials {

    .testimonials-customers-container {
        display        : flex;
        justify-content: space-evenly;
        align-items    : center;
        margin-bottom  : 65px;

        @media #{$maxTablet} {
            margin-bottom: 25px;
            flex-wrap    : wrap;
        }

        .author-img {
            display        : flex;
            justify-content: center;
            align-items    : center;
            overflow       : hidden;
            cursor         : pointer;
            margin-right   : 20px;
            transition     : 0.3s ease-in-out;
            opacity        : 0.5;
            width          : 90px;
            height         : 90px;
            border-radius  : 50%;

            img {
                transition: 0.3s ease-in-out;
            }

            &:last-child {
                margin-right: 0;
            }

            &.active {
                opacity: 1;

                img {
                    transform: scale(1.6);
                }
            }

            @media #{$maxTablet} {
                width : 70px;
                height: 70px;

                &:last-child {
                    margin-right: 23px;
                }
            }

            img {
                height       : 100%;
                border-radius: 50%;
                width        : 100%;
                transition   : 0.3s ease-in-out;
            }
        }
    }

    .testimonial-card {
        .testimonial-body {
            text-align: center;
        }

        .testimonial-name {
            font-family   : var(--global--font-body);
            font-size     : 20px;
            line-height   : 30px;
            font-weight   : 600;
            color         : var(--global--color-primary);
            text-transform: capitalize;
            margin-bottom : 0;
        }

        .testimonial-title {
            font-family   : var(--global--font-body);
            font-size     : 16px;
            line-height   : 1.8;
            font-weight   : 300;
            text-transform: capitalize;
            color         : var(--global--color-body);
            margin-bottom : 0px;
        }

        .testimonial-desc {
            font-family  : var(--global--font-body);
            font-size    : 26px;
            font-weight  : 500;
            line-height  : 34px;
            color        : var(--global--color-body);
            margin-bottom: 36px;

            @media #{$minXlarge} {
                max-width: 80%;
                margin   : 0 auto 36px;
            }

            @media #{$noteBook} {
                max-width   : 75%;
                margin-right: auto;
                margin-left : auto;
            }
        }

    }

    &.testimonials-2 {
        padding-top   : 111px;
        padding-bottom: 0px;

        @media #{$maxTablet} {
            padding-top   : 60px;
            padding-bottom: 0;
        }

        .owl-carousel {
            padding: 0 5%;

            @media #{$minLarge} {
                padding: 0 2%;
            }

            @media #{$minXlarge} {
                padding: 0 3%;
            }

            .owl-stage-outer {
                overflow: visible;

                .owl-item {
                    opacity: 0;

                    &.active {
                        opacity: 1;
                    }
                }
            }
        }

        .testimonial-card {
            background-color: var(--global--color-white);
            border-radius   : 23px;
            box-shadow      : 0 20px 50px 0 rgba(#000, 0.04);
            padding         : 40px 45px 30px;

            @media #{$noteBook} {
                padding-left : 25px;
                padding-right: 25px;
            }

            @media #{$maxSmall} {
                padding-left : 25px;
                padding-right: 25px;
            }

            .testimonial-body {
                display      : flex;
                align-items  : center;
                margin-bottom: 28px;

                .testimonial-img {
                    width        : 57px;
                    height       : 59px;
                    border-radius: 20px;
                    margin-right : 12px;
                }

                .testimonial-content {
                    .testimonial-name {
                        font-size    : 15px;
                        font-weight  : 600;
                        line-height  : 32px;
                        color        : var(--global--color-heading);
                        margin-bottom: 0;
                    }

                    .testimonial-title {
                        font-size  : 14px;
                        font-weight: 300;
                    }
                }
            }

            .testimonial-desc {
                max-width  : unset;
                margin     : 0;
                font-size  : 18px;
                font-weight: 400;
                line-height: 40px;
            }
        }

        .testimonial-background {
            width           : 100%;
            height          : 350px;
            border-radius   : 50px;
            background-color: var(--global--color-secondary);
            opacity         : 0.08;
            transform       : translateY(-227px);
            margin-bottom   : -227px;

            @media #{$maxTablet} {
                transform    : translateY(-260px);
                margin-bottom: -260px;
            }

            @media #{$maxSmall} {
                transform    : translateY(-280px);
                margin-bottom: -280px;
            }
        }
    }
}