/*------------------------------------*\
    #Call To Action
\*------------------------------------*/
.cta {
    position        : relative;
    overflow        : visible;
    padding-bottom  : 75px;

    @media #{$maxTablet} {
        padding-top   : 0;
        padding-bottom: 50px;
    }

    .contactForm {
        .btn {
            width : 220px;
            height: 65px;
            margin: 0 auto;
        }
    }
}

.form-mailchimp {
    position    : relative;
    margin-right: 0;
    margin-left : 0;

    .input-group {
        display         : flex;
        justify-content : space-between;
        border          : none;
        height          : 73px;
        padding         : 0;
        border-radius   : 13px;
        background-color: var(--global--color-white);
        border-color    : var(--global--color-white);
        position        : relative;
        overflow        : hidden;

        .btn {
            width        : 79px;
            border-radius: 13px;
            height       : 73px;
            line-height  : 73px;
            outline      : none;

            &::before {
                content: unset;
            }

            &:hover {
                background-color: rgb(255, 91, 7);
            }
        }

        .input-icon {
            display    : flex;
            align-items: center;
            flex-grow  : 1;

            .form-control {
                margin-bottom   : 0;
                border          : 0;
                height          : 73px;
                padding-left    : 15px;
                padding-right   : 15px;
            }
        }
    }
}

.cta-note {
    text-align: center;

    p {
        margin-bottom: 0;
        font-family  : var(--global--font-body);
        font-weight  : 400;
        font-size    : 16px;
        line-height  : 1.8;
        color        : var(--global--color-body);

        i {
            margin-right: 5px;
        }
    }
}

/* Download */
.download {
    position      : relative;
    padding-top   : 110px;
    padding-bottom: 110px;
    margin-bottom: 70px;
    @media #{$maxTablet} {
        padding-top   : 70px;
        padding-bottom: 70px;
        margin-bottom : 70px;
    }

    .download-overlay {
        position        : absolute;
        top             : 50%;
        left            : 50%;
        transform       : translate(-50%, -50%);
        width           : calc(100% - 5%);
        height          : 100%;
        background-color: var(--global--color-primary);
        z-index         : 0;
        border-radius   : 22px;
    }

    .heading {
        .heading-desc{
            @media screen and (min-width:991px) {
                padding:0 40px;
            }
        }
    }

    .download-action {
        display        : flex;
        justify-content: center;

        @media #{$maxSmall} {
            flex-direction: column;
            align-items   : center;
        }

        .btn {
            margin-left    : 30px;
            background-color: transparent;
            border-color    : #F8F8F8;

            &:hover,
            &:active,
            &:focus {
                background-color: #F8F8F8;
            }

            &:last-child {
                margin-left: 0;
            }

            @media #{$maxSmall} {
                margin-left : 0;
                margin-bottom: 25px;

                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
    }
}