/*------------------------------------*\
    #Blog
\*------------------------------------*/
.blog {
    .blog-card {

        @media #{$maxTablet} {
            margin   : 0 auto 30px;
            max-width: 370px;
        }

        @media #{$maxTablet} {
            max-width: 370px;
            margin   : 0 auto 40px;
        }

        &:hover {
            .blog-img {
                img {
                    transform: scale(1.1);
                }
            }
        }

        .blog-img {
            overflow     : hidden;
            border-radius: 18px;

            img {
                transition: 0.3s ease-in-out;
                max-width : 100%;
            }
        }

        .blog-content {
            padding-top     : 18px;
            border-radius   : 0 0 21px 21px;
            background-color: var(--global--color-white);

            .blog-meta-container {
                margin-bottom: 19px;

                div {
                    display: inline-block;

                    &:first-of-type {
                        margin-right: 16px;

                        @media #{$maxSmall} {
                            margin-bottom: 15px;
                        }
                    }

                    span {
                        font-family: var(--global--font-body);
                        font-size  : 13px;
                        font-weight: 400;
                        color      : var(--global--color-body);
                    }
                }

                .meta-date {
                    span {
                        text-transform: capitalize;
                    }
                }
            }

            .blog-title {
                font-family  : var(--global--font-body);
                font-size    : 20px;
                font-weight  : 600;
                line-height  : 30px;
                color        : var(--global--color-heading);
                margin-bottom: 16px;

                @media screen and (min-width:1200px) {
                    padding-right: 40px;
                }

                a {
                    color: var(--global--color-heading);

                    &:hover {
                        color: var(--global--color-primary);
                    }
                }
            }

            .blog-desc {
                font-size    : 16px;
                font-weight  : 400;
                line-height  : 1.8;
                margin-bottom: 20px;
            }

            .read-more {
                a {
                    font-size     : 16px;
                    font-weight   : 600;
                    line-height   : 26px;
                    text-transform: capitalize;
                    color         : var(--global--color-heading);
                    position      : relative;
                    transition    : 0.3s ease-in-out;

                    &:hover {
                        color: var(--global--color-primary);

                        i {
                            transform: translateX(7px);
                            color    : var(--global--color-primary);
                        }
                    }

                    i {
                        color      : var(--global--color-heading);
                        font-size  : 13px;
                        transition : 0.3s ease-in-out;
                        margin-left: 12px;
                    }
                }
            }
        }
    }
}