/*------------------------------------*\
    #Screenshots
\*------------------------------------*/

.screenshots {
    background-color: var(--global--color-primary);
    .screenshot-warp {
        position: relative;
        @media #{$maxSmall} {
            max-width: 320px;
            margin: 0 auto;
        }
        &:before {
            position       : absolute;
            left           : 50%;
            top            : -10px;
            transform      : translateX(-50%);
            background     : url('../images/screenshots/mockup-light.png') no-repeat;
            background-size: contain;
            content        : '';
            width          : 320px;
            height         : 710px;
            z-index        : 3;
            @media #{$maxSmall} {
                height: 653px;
            }
            @media screen and (min-width:992px) and (max-width:1200px) {
                width: 276px;
            }
        }
    }

    .container {
        @media screen and (max-width: 767.98px) {
            max-width: unset;
        }
    }
    .owl-carousel {
        padding-top   : 55px;
        padding-bottom: 55px;

        
        @media screen and (max-width:991px) {
            padding-top: 45px;
        }
        @media screen and (max-width:767px) {
            padding-top: 0;
        }
        
        @media screen and (min-width:992px) and (max-width:1200px) {
            padding-top: 48px;
        }

        .owl-stage-outer {
            overflow: visible;

            .owl-item {
                border-radius: 18px;
                opacity      : 0;

                img {
                    border-radius: 18px;

                    @media#{$maxTablet} {
                        text-align  : center;
                        margin-right: auto;
                        margin-left : auto;
                    }

                    @media screen and (max-width:767px) {
                        max-width : 300px;
                        max-height: 710px;
                        margin: 0 auto;
                    }

                    @media screen and (max-width:991px) {
                        max-width : 300px;
                        max-height: 615px;
                    }
                    @media #{$maxSmall} {
                        height: 653px;
                    }
                }

                &.active {
                    opacity   : 1;
                    box-shadow: 0px 0px 99px rgba(#000000, 0.16);
                    transition: 0.3s ease-in-out;

                    @media screen and (max-width:767px) {
                        box-shadow: none;
                    }

                }

                &.center {

                    z-index   : 2;
                    box-shadow: 10px 10px 99px rgba(#000000, 0.33);
                    transform: scale(1.2);
                    @media #{$maxSmall} {
                        height: 600px;
                        transform: scale(1)
                    }
                }

                &:not(.center) {
                    &:before {
                        position       : absolute;
                        left           : -22px;
                        top            : -19px;
                        background     : url('../images/screenshots/mockup-light.png') no-repeat;
                        background-size: contain;
                        content        : '';
                        width          : calc(100% + 50px);
                        height         : calc(100% + 38px);
                        z-index        : 3;

                        @media screen and (min-width:992px) and (max-width:1200px) {
                            left: -18px;
                        }
                    }
                }
            }
        }
        &.carousel-navs {
            .owl-nav {
                @media #{$maxSmall} {
                    display : block;
                }
                .owl-prev,
                .owl-next {
                    width          : 45px;
                    height         : 45px;
                    display        : flex;
                    align-items    : center;
                    justify-content: center;
                    border-radius  : 8px;
                    display:none;
                    @media #{$maxSmall} {
                        display : flex;
                    }
                    &::before {
                        content: unset;
                    }
                }
    
                .owl-prev {
                    background      : url('../images/icons/chevron-left.png') no-repeat center;
                    background-color: #fff;
                    left            : -85px;
                    transform       : rotate(0deg);
    
                    @media #{$noteBook} {
                        left: -40px;
                    }
                    @media #{$maxSmall} {
                        left: -40px;
                    }
                }
    
                .owl-next {
                    background      : url('../images/icons/chevron-right.png') no-repeat center;
                    background-color: #fff;
                    right           : -85px;
                    transform       : rotate(0deg);
    
                    @media #{$noteBook} {
                        right: -40px;
                    }
                    @media #{$maxSmall} {
                        right: -40px;
                    }
                }
            }
        }

    }

    &.screenshots-2 {
        padding-top   : 140px;
        padding-bottom: 128px;

        @media #{$maxTablet} {
            padding-top   : 20px;
            padding-bottom: 70px;
        }

        .heading {
            z-index : 2;
            position: relative;

            @media #{$minLarge} {
                padding-left: 15%;
            }

            @media #{$maxTablet} {
                padding: 0 5%;
            }
        }

        .owl-carousel {
            @media #{$maxTablet} {
                padding: 0 5%;
            }

            .owl-stage-outer {
                .owl-item {
                    &.active {
                        @media #{$minLarge} {
                            opacity: 0.7;
                        }
                    }
                }

                .owl-item:not(.active)+.owl-item.active {
                    @media #{$minLarge} {
                        z-index   : 2;
                        opacity   : 1;
                        box-shadow: 10px 10px 99px rgba(#000000, 0.33);

                        @media #{$minTablet} {
                            transform: scale(1.2);
                        }
                    }
                }
            }
        }
    }
}