/*------------------------------------*\
    #Heading
\*------------------------------------*/

.heading {
    margin-bottom: 56px;
    position     : relative;

    @media #{$maxTablet} {
        text-align: center;
    }

    .heading-subtitle {
        font-family   : var(--global--font-body);
        font-size     : 15px;
        font-weight   : 700;
        line-height   : 30px;
        margin-bottom : 7px;
        text-transform: capitalize;
    }

    .heading-title {
        font-size     : 34px;
        line-height   : 1.5;
        font-weight   : 500;
        margin-bottom : 13px;
        text-transform: capitalize;
        color         : var(--global--color-primary);

        @media #{$maxSmall} {
            font-size  : 32px;
            line-height: 1.4;
        }
    }

    .heading-desc {
        font-size    : 16px;
        font-weight  : 400;
        line-height  : 1.8;
        margin-bottom: 0;
        padding      : 0;
        color        : var(--global--color-body);
    }

    &.heading-light {
        .heading-subtitle,
        .heading-desc {
            color: #fefeff;
        }

        .heading-title {
            color: var(--global--color-white);
        }
    }
    &.heading-2 {
        margin-bottom: 40px;
        .heading-title {
            margin-bottom:0
        }
    }
}