/*------------------------------------*\
    #Footer
\*------------------------------------*/
.footer {
    padding-top: 60px;
    position   : relative;
    background-color: #34383C;
    @media #{$maxTablet} {
        padding-top: 50px;
    }

    .footer-top {
        padding-bottom: 57px;

        @media #{$maxTablet} {
            padding-bottom: 0px;
        }

        .footer-logo {
            margin-bottom: 20px;

            img {
                max-width: 100%;
                height   : auto;
                width    : 180px;
            }
        }

        .footer-widget {
            @media #{$maxTablet} {
                margin-bottom: 30px;
            }
            .widget-title {
                font-size     : 18px;
                line-height   : 1.5;
                font-weight   : 500;
                color         : var(--global--color-white);
                text-transform: capitalize;
                position      : relative;
                margin-bottom : 30px;
            }
        }

        .widget-about {
            .logo {
                max-width: 300px;
                margin-bottom: 30px;
                img {
                    max-width: 100%;
                }
            }
            .vat-section {
                display: flex;
                align-items: center;
                gap: 30px;
                margin-bottom: 30px;
                img {
                    max-width: 100%;
                }
                a {
                    img {
                        width: 125px;
                    }
                }
                div {
                    img {
                        width: 70px;
                    }
                }
            }
            .footer-social {
                display    : flex;
                align-items: center;

                span {
                    font-family   : var(--global--font-heading);
                    font-size     : 16px;
                    line-height   : 1.8;
                    color         : var(--global--color-white);
                    text-transform: capitalize;
                    display       : inline-block;
                    margin-left  : 20px;
                }

                ul {
                    margin-bottom: 0;

                    li {
                        display     : inline-block;
                        margin-left: 20px;

                        &:last-child {
                            margin-left: 0;
                        }

                        a {
                            display        : inline-flex;
                            justify-content: center;
                            align-items    : center;
                            font-size      : 16px;
                            color          : rgba(255, 255, 255, .8);
                            transition     : 0.3s ease-in-out;

                            &:hover {
                                color: var(--global--color-primary);
                            }
                        }
                    }
                }

            }
        }

        .widget-links {
            ul {
                padding: 0;
            }
            a {
                font-size     : 14px;
                font-weight   : 400;
                line-height   : 21px;
                color         : rgba(255, 255, 255, .8);
                text-transform: capitalize;
                display       : inline-flex;
                align-items   : center;
                margin-bottom : 11px;
                transition    : 0.3s ease-in-out;

                &:hover {
                    color: var(--global--color-primary);
                }
            }

            li {
                &:last-child {
                    a {
                        margin-bottom: 0;
                    }
                }
            }
        }

        .widget-newsletter {
            p {
                font-size     : 16px;
                line-height   : 1.8;
                font-weight: 400;
                color         : rgba(255, 255, 255, .8);
                text-transform: capitalize;
                margin-bottom : 30px;

                @media #{$minTablet} {
                    margin-left: 20%;
                }
            }

            .form-mailchimp {
                position    : relative;
                margin-right: 0;
                margin-left : 0;

                .input-group {
                    display         : flex;
                    justify-content : space-between;
                    border          : none;
                    height          : 50px;
                    padding         : 0;
                    border-radius   : 6px;
                    background-color: var(--global--color-white);
                    border-color    : var(--global--color-white);
                    position        : relative;
                    overflow        : hidden;
                    max-width: 280px;

                    .btn {
                        width        : 50px;
                        border-radius: 0;
                        height       : 50px;
                        line-height  : 50px;
                        outline      : none;

                        svg{
                            height: 18px;
                            transform: rotate(180deg);
                        }

                        &::before {
                            content: unset;
                        }

                        &:hover {
                            background-color: rgb(0, 0, 0);
                        }
                    }

                    .input-icon {
                        display    : flex;
                        align-items: center;
                        flex-grow  : 1;

                        .form-control {
                            margin-bottom   : 0;
                            border          : 0;
                            height          : 50px;
                            padding-left    : 15px;
                            padding-right   : 15px;
                            line-height     : normal;
                            background-color: #fff;
                        }
                    }
                }
            }

        }
    }

    hr {
        border-color: rgba(255, 255, 255, .15);
    }

    .footer-bottom {
        padding-top   : 20px;
        padding-bottom: 20px;
        display    : flex;
        align-items: center;

        .footer-copyright {
            text-align: center;
            margin-bottom: 20px;
            a,
            span {
                font-size     : 14px;
                font-weight   : 400;
                line-height   : 26px;
                text-transform: capitalize;
                color         : var(--global--color-light);
            }

            a {
                &:hover {
                    color: var(--global--color-primary);
                }
            }
        }
        .footer-payments {
            display: flex;
            justify-content: center;
            align-items: center;
            ul {
                padding: 0;
                margin: 0;
                li {
                    display: inline-block;
                    margin-left: 10px;
                    &:last-child {
                        margin-left: 0px;
                    }
                    a {
                        display: block;
                        width: 54px;
                        height: 25px;
                        img {
                            display: block;
                            width: 100%;
                            height: 100%;
                            object-fit: cover;
                        }
                    }
                }
            }
        }

    }

}

/* Custom, iPhone Retina */
@media only screen and (min-width: 320px) and (max-width: 767px) {
    .footer-copyright {
        font-size: 12px;
    }
}