/*------------------------------------*\
    #Menu
\*------------------------------------*/
.header {
	.navbar {
		margin-bottom: 0;
		border-radius: 0;
		min-height   : 90px;
		border       : none;
		padding      : 0;
		@media #{$maxTablet} {
			background: var(--global--color-white);
			box-shadow: 0 2px 4px rgba(3, 27, 78, 0.1);
			padding: 0 15px;
		}
		.navbar-brand {
			margin-right: 0;
			padding: 0;
			width: 96px;
			height: 70px;
			.logo {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
			.logo-dark {
				display: none;
				@media #{$maxTablet} {
					display: inline-block;
				}
			}

			.logo-light {
				display: inline-block;
				@media #{$maxTablet} {
					display: none;
				}
			}
		}

		.navbar-nav {
			>li {
				margin-right: 35px;
				position    : relative;
				transition  : 0.3s ease-in-out;

				>a {
					font-family   : var(--global--font-body);
					font-size     :16px;
					text-transform: capitalize;
					font-weight   : 500;
					line-height   :24px;
					color         : var(--global--color-white);
					padding       : 0;
					position      : relative;
					display       : block;
					transition    : 0.3s ease-in-out;

					@media #{$minTablet} {
						&::before {
							position        : absolute;
							left            : 0;
							bottom          : -5px;
							content         : '';
							height          : 3px;
							width           : 0;
							background-color: var(--global--color-white);
							transition      : width 300ms ease-in-out;
						}
					}
				}

				&.active>a,
				>a:focus,
				>a:hover {

					@media #{$minLarge} {
						&:before {
							width           : 50%;
						}
					}
				}

				@media #{$maxTablet} {
					margin-right : 0;
					margin-bottom: 10px;
					border-radius: 4px;
					&:last-child {
						margin-bottom: 0;
					}
					a {
						margin-right: 0;
						padding     : 15px 10px;
						color       : var(--global--color-heading);
						line-height : 1;
					}

					&.active,
					&:hover,
					&:focus,
					&:active {
						background-color: var(--global--color-primary);

						a {
							color: var(--global--color-white)
						}
					}
				}
			}
		}

		&.navbar-fixed {
			top       : 0;
			background: var(--global--color-white);
			box-shadow: 0 2px 4px rgba(3, 27, 78, 0.1);
			animation : navbarFixed 0.8s;

			.navbar-brand {
				.logo-dark {
					display: inline-block;
				}

				.logo-light {
					display: none;
				}
			}

			.navbar-nav {
				>li {
					>a {
						color: var(--global--color-heading);
						&::before {
							background-color: var(--global--color-primary)
						}
					}

					&.active,
					&:hover,
					&:focus,
					&:active {
						@media #{$maxTablet} {
							a {
								color: var(--global--color-white)
							}
						}
					}
				}
			}
		}
	}
	&.header-dark {
        .navbar {
            .navbar-nav {
                > li {
                    > a {
                        @media #{$minLarge} {
                            color: var(--global--color-heading);
                        }
                    }
                }
            }
        }
    }
}

@keyframes navbarFixed {
	0% {
		opacity  : 0;
		transform: translateY(-100%);
	}

	100% {
		opacity  : 1;
		transform: translateY(0);
	}
}