/*------------------------------------*\
    #Align
\*------------------------------------*/
address,
blockquote,
dd,
dl,
fieldset,
form,
ol,
p,
pre,
table,
ul {
  margin-bottom: $baseline;
}

section {
  padding-top   : 100px;
  padding-bottom: 100px;
  overflow      : hidden;

  @media #{$maxTablet} {
    padding-top   : 65px;
    padding-bottom: 65px;
  }
}