/*------------------------------------*\
    #Features
\*------------------------------------*/
.features {

    @media #{$maxTablet} {
        padding-bottom: 30px;
    }
}

.feature-card {
    position      : relative;
    box-shadow    : none;
    cursor        : pointer;
    display       : flex;
    flex-direction: column;
    align-items   : center;
    transition    : 0.3s ease-in-out;
    margin-bottom: 30px;
    @media #{$maxTablet} {
        max-width: 370px;
        margin   : 0 auto 30px;
    }

    @media #{$maxSmall} {
        flex-direction: column;
        align-items   : center;
    }
    &:hover {
        .feature-icon {
            transform: translateY(-5px);
        }
    }

    .feature-icon {
        flex-shrink     : 0;
        height          : 65px;
        width           : 65px;
        display         : flex;
        justify-content : center;
        align-items     : center;
        margin-bottom   : 19px;
        transition: 0.3s ease-in-out;
    }

    .feature-content {
        flex-grow : 1;
        text-align: center;

        h3 {
            font-size     : 20px;
            font-weight   : 500;
            line-height   : 1.5;
            margin-bottom : 10px;
            text-transform: capitalize;
            transition    : 0.3s ease-in-out;
        }

        p {
            font-size    : 16px;
            font-weight  : 400;
            line-height  : 1.8;
            color        : var(--global--color-body);
            margin-bottom: 0;
            transition   : 0.3s ease-in-out;
        }
    }

}