/*------------------------------------*\
    #Processes
\*------------------------------------*/
.processes {
    .process-card {
        padding   : 0 20px;
        transition: 0.4s ease-in-out;
        position  : relative;
        z-index   : 2;
        cursor    : pointer;

        @media#{$maxTablet} {
            text-align: center;
            max-width : 370px;
            margin    : 0 auto 30px;
        }

        .process-content {
            display       : flex;
            align-items   : center;
            flex-direction: column;

            .process-phase {
                margin-bottom   : 40px;
                width           : 90px;
                height          : 90px;
                display         : flex;
                align-items     : center;
                justify-content : center;
                border-radius   : 8px;
                background-color: #FFF2EB;
                transition      : all 300ms ease-in-out;

                p {
                    text-transform: capitalize;
                    font-family   : var(--global--font-body);
                    font-size     : 47px;
                    font-weight   : 600;
                    color         : var(--global--color-primary);
                    margin-bottom : 0;
                }
            }

            .process-title {
                font-family   : var(--global--font-body);
                text-transform: capitalize;
                font-weight   : 600;
                font-size     : 20px;
                line-height   : 36px;
                color         : var(--global--color-heading);
                margin-bottom : 10px;
            }

            .process-desc {
                font-family  : var(--global--font-body);
                font-size    : 16px;
                font-weight  : 400;
                line-height  : 1.8;
                margin-bottom: 0;
                color        : var(--global--color-body)2;
            }
        }

        &:hover {
            .process-content {
                .process-phase {
                    box-shadow: 0 20px 50px 0 rgba(#FF7D3B, 0.16);
                }
            }
        }

        &.process-card-1 {
            .process-content {
                .process-phase {
                    position: relative;

                    &::after {
                        @media #{$minLarge} {
                            content              : '';
                            position             : absolute;
                            top                  : 50%;
                            transform            : translateY(-50%);
                            left                 : 100%;
                            z-index              : 1;
                            background-image     : url('../images/line/line-top.png');
                            background-size      : contain;
                            display              : flex;
                            justify-content      : center;
                            align-items          : center;
                            width                : 200px;
                            height               : 26px;
                            background-repeat    : no-repeat;
                            background-position-x: center;

                            @media #{$minXlarge} {
                                left: 125%;
                            }
                        }
                    }
                }
            }
        }

        &.process-card-2 {
            .process-content {
                .process-phase {
                    background-color: var(--global--color-primary);
                    box-shadow      : 0 20px 50px 0 rgba(#FF7D3B, 0.16);
                    position        : relative;

                    p {
                        color: var(--global--color-white);
                    }

                    &::after {
                        @media #{$minLarge} {
                            content              : '';
                            position             : absolute;
                            top                  : 72%;
                            transform            : translateY(-50%);
                            left                 : 100%;
                            z-index              : 1;
                            background-image     : url('../images/line/line-bottom.png');
                            background-size      : contain;
                            display              : flex;
                            justify-content      : center;
                            align-items          : center;
                            width                : 200px;
                            height               : 26px;
                            background-repeat    : no-repeat;
                            background-position-x: center;

                            @media #{$minXlarge} {
                                left: 125%;
                            }
                        }
                    }
                }
            }
        }
    }

    &.processes-2 {
        padding-bottom: 42px;

        @media #{$maxTablet} {
            padding-bottom: 2px;
        }
    }
}