/*------------------------------------*\
    #Hero
\*------------------------------------*/

.hero {
    overflow        : visible;
    position        : relative;
    padding         : 0 0 50px 0;
    background-color: #96248E;

    .hero-cotainer {
        position   : relative;
        display    : flex;
        min-height : 900px;
        align-items: center;

        @media #{$maxTablet} {
            height     : auto;
            padding-top: 150px;
            padding-bottom: 50px;
        }

        .hero-content {
            display        : flex;
            flex-direction : column;
            justify-content: center;
            align-items    : flex-start;
            text-align     : start;
            flex-grow      : 1;
            @media screen and (min-width:991px) {
                padding-left: 60px;
            }

            @media screen and (max-width:767px) {
                align-items  : center;
                text-align   : center;
            }

            .hero-title {
                font-size    : 45px;
                line-height  : 60px;
                margin-bottom: 14px;
                color        : var(--global--color-white);

                @media #{$maxSmall} {
                    font-size  : 34px;
                    line-height: 1.2;
                }

                @media screen and (min-width:768px) and (max-width:991px) {
                    font-size  : 38px;
                    line-height: 48px;
                }
            }

            .hero-desc {
                font-size    : 16px;
                line-height  : 1.8;
                margin-bottom: 35px;
                color        : var(--global--color-white);
            }

            .download-action {
                display        : flex;
                justify-content: center;
                margin-bottom:30px;
                @media #{$maxTablet} {
                    flex-direction: column;
                    align-items   : center;
                }

                .btn {
                    margin-left     : 30px;
                    background-color: transparent;
                    border-color    : #F8F8F8;

                    &:hover,
                    &:active,
                    &:focus {
                        background-color: #F8F8F8;
                    }

                    &:last-child {
                        margin-left: 0;
                    }

                    @media #{$maxTablet} {
                        margin-left  : 0;
                        margin-bottom: 25px;

                        &:last-child {
                            margin-bottom: 0;
                        }
                    }
                }
            }

            .video-actions {
                width: 100%;
                .video-heading {
                    font-size    : 32px;
                    line-height  : 1.5;
                    margin-bottom: 20px;
                    color        : var(--global--color-white);
                }
                .video-wrapper {
                    .player {
                        border-radius  : 12px;
                        height         : 130px;
                        position       : relative;
                        z-index        : 1;
                        overflow       : visible;
                        display        : flex;
                        justify-content: center;
                        align-items    : center;
                        @media #{$maxSmall} {
                            margin-bottom: 20px;
                        }
                        
                        &::before {
                            border-radius: 12px;
                        }
                        .img-holder {
                            position     : absolute;
                            top          : 0;
                            left         : 0;
                            right        : 0;
                            width        : 100%;
                            height       : 100%;
                            overflow     : hidden;
                            border-radius: 12px;
                            box-shadow   : 10px 30px 50px 0 rgba(#000, 0.04);
                        }
                    }
                }
            }

        }

        .hero-img {
            overflow: hidden;
            @media #{$maxSmall} {
                display: none;
            }
            img {
                max-width: 100%;
                margin   : 0 auto;
            }
        }
    }

    &.hero-light {
        .hero-cotainer {
            .hero-content {
                .hero-headline {
                    color: var(--global--color-white);
                }

                .hero-bio {
                    color: #f8f9fa;
                }

            }
        }
    }

    &.hero-lead-2 {
        background-color: #F2F0FB;

        .hero-cotainer {
            min-height: unset;

            .hero-content {
                text-align : center;
                padding-top: 200px;

                @media #{$maxTablet} {
                    padding-top: 0;
                }

                .hero-title {
                    font-size    : 60px;
                    line-height  : 90px;
                    margin-bottom: 12px;

                    @media #{$maxSmall} {
                        font-size  : 32px;
                        line-height: 1.5;
                    }
                }

                .hero-desc {
                    margin-bottom: 25px;
                }
            }
        }
    }
}

.shape-divider-bottom {
    position   : absolute;
    bottom     : -50px;
    left       : 0;
    width      : 100%;
    overflow   : hidden;
    line-height: 0;

    svg {
        fill: #fff;
    }
}