/*------------------------------------*\
    #Pricing
\*------------------------------------*/
.pricing {
    position      : relative;
    padding-bottom: 100px;

    @media #{$maxTablet} {
        padding-top   : 55px;
        padding-bottom: 50px;
    }

    .pricing-switcher {
        display        : flex;
        justify-content: center;
        align-items    : center;
        margin-bottom  : 47px;

        input {
            display: none;
        }

        label {
            font-family   : var(--global--font-body);
            font-size     :18px;
            font-weight   : 500;
            line-height   : 25px;
            text-transform: capitalize;
            cursor        : pointer;
            transition    : 0.5s ease-in-out;
            color         : var(--global--color-heading);
            margin-bottom: 0;

            @media #{$maxSmall} {
                font-size: 16px;
            }

            &:hover,
            &.active {
                color: var(--global--color-heading);
            }
        }

        .indicator {
            width           : 55px;
            height          : 25px;
            background-color: var(--global--color-secondary);
            border-radius   : 22px;
            margin          : 0 10px;
            position        : relative;
            cursor          : pointer;
            display         : flex;
            align-items     : center;
            padding         : 5px;

            @media #{$maxSmall} {
                width : 70px;
                margin: 0 15px;
            }

            .ball {
                display         : inline-block;
                width           : 19px;
                height          : 19px;
                background-color: var(--global--color-white);
                border-radius   : 50%;
                transition      : 0.5s ease-in-out;
                transform       : translateX(0px);

                &.yearly {
                    transform: translateX(-26px);

                    @media #{$maxSmall} {
                        transform: translateX(-35px);
                    }
                }

                &.monthly {
                    transform: translateX(0);
                }
            }
        }

    }

    .pricing-container {
        display: flex;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        border-radius: 6px;
    }
    .pricing-heads {
        flex-basis: 25%;;
        min-width: 200px;
        flex-shrink: 0;
        background-color: var(--global--color-white);
        border          : 3px solid #EBEEEE;
        padding         : 191px 45px 45px;
        transition      : all 0.3s ease-in-out;
        position        : relative;
        border-radius   : 19px;
        text-align: center;
        @media #{$noteBook} {
            padding-left : 30px;
            padding-right: 30px;
        }

        @media #{$maxSmall} {
            padding-left : 30px;
            padding-right: 30px;
        }
        .pricing-list {
            list-style: none;
            margin:0;
            padding: 0;
            li {
                display: flex;
                align-items: center;
                height: 49px;
                font-family   : var(--global--font-body);
                font-size     : 16px;
                line-height   : 1.8;
                color:var(--global--color-primary);
                font-weight   : 400;
                text-align    : right;
                text-transform: capitalize;
                padding-bottom: 20px;

                i {
                    margin-left: 9px;
                    font-size: 13px;
                    color:#EBEEEE;
                }
                &:last-child {
                    padding-bottom: 0;
                }
            }
        }
    }

    .pricing-table {
        position: relative;
        flex-basis: 25%;
        min-width: 200px;
        flex-shrink: 0;
        @media #{$maxTablet} {
            max-width: 370px;
            margin   : 0 auto 30px;
        }

        .pricing-card {
            background-color: var(--global--color-white);
            border          : 3px solid #EBEEEE;
            padding         : 45px 45px 45px;
            transition      : all 0.3s ease-in-out;
            position        : relative;
            border-radius   : 19px;
            text-align: center;
            @media #{$noteBook} {
                padding-left : 30px;
                padding-right: 30px;
            }

            @media #{$maxSmall} {
                padding-left : 30px;
                padding-right: 30px;
            }

            &.active {
                background-color: var(--global--color-secondary);
                border-color    : var(--global--color-secondary);

                .pricing-head {
                    .pricing-type {
                        .per {
                            color: var(--global--color-light);
                        }

                        .price {
                            color: var(--global--color-light);

                            &::before {
                                color: var(--global--color-light);
                            }
                        }
                    }

                    .pricing-name {
                        color: var(--global--color-light);
                    }

                    .pricing-desc {
                        color: var(--global--color-light);
                    }
                }

                .pricing-body {
                    .pricing-list {
                        li {
                            i {
                                color: #757575;
                            }
                            &.active{
                                i {
                                    color: var(--global--color-light);
                                }
                            }

                            &.unavailable {
                                svg {
                                    color: #C3BADF;
                                }
                            }
                        }
                    }

                    .btn {
                        background-color: var(--global--color-primary);
                        border-color    : var(--global--color-primary);
                        color           : #fff;

                        &:hover,
                        &:active,
                        &:focus {
                            background-color: var(--global--color-white);
                            border-color    : var(--global--color-white);
                            color           : var(--global--color-primary);
                        }
                    }
                }
            }

            &.visible {
                position: relative;
                z-index : 5;
            }

            &.hidden {
                position : absolute;
                top      : 0;
                left     : 0;
                height   : 100%;
                width    : 100%;
                z-index  : 1;
                transform: rotateY(180deg);
            }

            .pricing-head {
                position      : relative;
                display       : flex;
                flex-direction: column;
                align-items   : center;
                text-align: center;
                position      : relative;
                overflow      : hidden;
                margin-bottom : 42px;

                .pricing-type {
                    margin-bottom: 10px;
                    text-align   : center;
                    display      : flex;
                    align-items  : baseline;

                    .per {
                        font-family   : var(--global--font-body);
                        font-size     : 14px;
                        font-weight   : 400;
                        line-height   : 18px;
                        color         : var(--global--color-primary);
                        text-transform: lowercase;
                        margin-bottom : 0;
                        transform     : translateY(6px);
                    }

                    .price {
                        font-family  : var(--global--font-body);
                        font-weight  : 600;
                        font-size    : 48px;
                        line-height  : 48px;
                        color        : var(--global--color-primary);
                        position     : relative;
                        padding-left : 19px;
                        margin-bottom: 0;
                        @media #{$maxSmall} {
                            font-size    : 28px;
                        }
                        &::before {
                            content    : 'SAR';
                            position   : absolute;
                            font-family: var(--global--font-body);
                            font-weight: 400;
                            font-size  : 14px;
                            color      : var(--global--color-primary);
                            line-height: 45px;
                            top        : 0;
                            left       : -13px;
                        }
                        &.text {
                            font-size : 32px;
                            line-height : 1.5;
                            @media #{$maxSmall} {
                                font-size    : 22px;
                                line-height   :48px;
                            }
                            &::before {
                                content: unset;
                            }
                        }
                    }
                }

                .pricing-name {
                    font-family   : var(--global--font-body);
                    font-size     : 26px;
                    font-weight   : 600;
                    line-height   : 36px;
                    text-transform: capitalize;
                    color         : var(--global--color-heading);
                    margin-bottom : 10px;
                }

                .pricing-desc {
                    font-family   : var(--global--font-body);
                    font-size     : 16px;
                    font-weight   : 400;
                    line-height   : 1.8;
                    text-transform: capitalize;
                    color         : var(--global--color-body);
                    margin-bottom : 0px;
                }
            }

            .pricing-body {
                text-align: center;

                .pricing-list {
                    margin-bottom: 45px;

                    li {
                        display: flex;
                        align-items: center;
                        justify-content : center;
                        height: 49px;
                        font-family   : var(--global--font-body);
                        font-size     : 16px;
                        line-height   : 1.8;
                        color         : var(--global--color-heading);
                        font-weight   : 400;
                        text-align    : center;
                        text-transform: capitalize;
                        padding-bottom: 20px;

                        i {
                            margin-left: 9px;
                            font-size: 13px;
                            color:#EBEEEE;
                        }

                        &.active{
                            i{
                                color:var(--global--color-primary);
                            }
                        }

                        &:last-child {
                            padding-bottom: 0;
                        }
                    }
                }

                .btn {
                    width : 100%;
                    height: 51px;
                }
            }
        }

        &.pricing-table-2 {
            .pricing-card {
                &.active {
                    box-shadow      : 0 20px 50px 0 rgba(#451CC0, 0.2);
                    border-color    : var(--global--color-secondary);
                    background-color: var(--global--color-white);

                    .pricing-head {
                        .pricing-type {
                            .per {
                                color: var(--global--color-secondary);
                            }

                            .price {
                                color: var(--global--color-secondary);

                                &::before {
                                    color: var(--global--color-secondary);
                                }
                            }
                        }

                        .pricing-name {
                            color: var(--global--color-secondary);
                        }

                        .pricing-desc {
                            color: var(--global--color-secondary);
                        }
                    }

                    .pricing-body {
                        .pricing-list {
                            li {
                                color: var(--global--color-secondary);

                                &.active{
                                    svg {
                                        color: var(--global--color-secondary);
                                    }
                                }

                                &.unavailable {
                                    svg {
                                        color: rgba(#451CC0, 0.42);
                                    }
                                }
                            }
                        }

                        .btn {
                            background-color: var(--global--color-secondary);
                            border-color    : var(--global--color-secondary);
                            color           : #fff;
                            box-shadow      : 0 10px 50px rgba(#000, 0.08);

                            &:hover,
                            &:active,
                            &:focus {
                                background-color: var(--global--color-white);
                                border-color    : var(--global--color-secondary);
                                color           : var(--global--color-secondary);
                            }
                        }
                    }
                }
            }
        }
    }
}