.video {
    
    .video-card {
        display: flex;
        margin-bottom: 40px;
        .player {
            flex-basis: 50%;
            flex-shrink: 0;
            border-radius  : 12px;
            height         : 130px;
            position       : relative;
            z-index        : 1;
            overflow       : visible;
            display        : flex;
            justify-content: center;
            align-items    : center;
            margin-left: 30px;
            @media #{$maxSmall} {
            }
            
            &::before {
                border-radius: 12px;
            }
            .img-holder {
                position     : absolute;
                top          : 0;
                left         : 0;
                right        : 0;
                width        : 100%;
                height       : 100%;
                overflow     : hidden;
                border-radius: 12px;
                box-shadow   : 10px 30px 50px 0 rgba(#000, 0.04);
            }
        }
        .video-info {
            .video-title {
                font-family   : var(--global--font-body);
                font-size     : 18px;
                font-weight   : 700;
                line-height   : 30px;
                margin-bottom : 7px;
                text-transform: capitalize;
                color        : var(--global--color-primary);
            }
            .video-desc {
                font-size    : 16px;
                font-weight  : 400;
                line-height  : 1.5;
                margin-bottom: 0;
                padding      : 0;
                color        : var(--global--color-body);
            }
        }
    }
}